.App {
  text-align: center;
}

a {
  text-decoration: none;
  color: black;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

/* body{
  overflow: hidden;
} */

.banner {
  display: flex;
  width: 82vw;
  height: 30vh;
  justify-content: space-between;
  background: #ffb5a7;
}

.banner_head {
  font-size: 40px;
  font-weight: 600;
  padding: 50px;
  padding-inline: 50px;
  color: white;
  text-align: right;
  line-height: 40px;
}

.banner div span {
  font-size: 29px;
  text-align: center;
}

.banner img {
  aspect-ratio: 16/9;
  height: 30vh;
}

.nav {
  display: flex;
  align-content: center;
  justify-content: center;
  font-size: large;
  padding-inline: 0px;
  width: 18vw;
  height: 100vh;
  background-color: #564ed7;
  color: white;
  position: fixed;
}
.nav ul li a {
  color: white;
}

.nav ul li a:hover {
  color: #b8bfc8;
}
.blank {
  width: 18vw;
  height: 100vh;
}

.main {
  display: flex;
  overflow-x: hidden;
}
.new_temp {
  display: flex;
  justify-content: center;
}
.temp_par {
  display: flex;
}
.temp {
  display: flex;
  flex-direction: column;
  margin: 10px;
  align-items: center;
}
.temp img {
  width: 11vw;
  aspect-ratio: 1/1;
  border-radius: 10px;
}
.temp span {
  width: 100px;
  text-align: center;
}
.butn_def {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}
#carosuel {
  margin-top: 20px;
}

.left {
  margin-left: 18vw;
}

#first {
  color: #6c757d;
  font-size: 20px;
  font-weight: 600;
}
#sec {
  display: inline-block;
  width: 50vw;
  background-color: #6c757d;
  height: 1.2px;
  margin: 10px;
}
#spinner {
  color: #736cd8;
}
.Jus-Con {
  max-width: min-content;
}
#head {
  align-items: center;
  display: flex;
  margin-block: 12px;
}
.pannel {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-block: 50px;
  overflow-x: hidden;
}
.butn {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  color: #736cd8;
  font-size: x-large;
  margin-inline: 3px;
}
.butn:hover {
  color: #736cd8;
}

.inputbox input {
  border: 1.5px solid #c1c1c1;
  width: 30vw;
  height: 6.5vh;
  border-radius: 100px;
  /* padding: 22px; */
  padding-inline: 21px;
}
.inputbox {
  display: flex;
    width: 100%;
    margin-top: 20px;
    margin-bottom: 20px;
    justify-content: center;
    align-content: center;
    align-items: center;
  
}
.main_search_box {
  display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;}

.search_butn {
  font-size: 150%;
  padding: 10px;
  color: #564ed7;
}
.search_aud_butn {
  font-size: 3.75vh;
  padding: 10px;
  color: #564ed7;
}
.item {
  display: flex;
  width: 70%;
  margin: 20px;
  align-items: center;
  justify-content: flex-start;
  height: fit-content;
}

.item img {
  width: 33%;
  aspect-ratio: 1/1;
  border-radius: 8px;
}
.item div {
  width: 23.4vw;
  padding: 15px;
  margin-left: 15px;
}
.list_main {
  max-height: 60vh;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  /* flex-wrap: wrap; */
  align-items: center;
}
.list_main::-webkit-scrollbar {
  display: none;
}

.video {
  aspect-ratio: 16/9;
  width: 70%;
  justify-content: center;
}
.modal-content{
  height: 90vh;
}
.lyric_head {
  justify-content: center;
  height: 30%;
  overflow-y: auto;
  text-align: center;
}
.lyrics {
  list-style: none;
  line-height: 1.9rem;
  font-weight: 600;
}

.lyrics_heading {
  color: rgb(240 98 59);
  text-shadow: -1.5px -1.5px #564ed7;
  text-align: center;
  /* font-weight: 700; */
}

.play_butn {
  color: rgb(240 98 59);
  font-size: 125%;
}
.play_butn:hover {
  color: rgba(239, 133, 103, 0.9);
}
.hide {
  display: none;
  position: fixed;
}
.txt:hover + .hide {
  display: block;
  width: 100px;
  border: 1px solid rgb(114, 111, 111);
  background-color: rgb(188, 184, 184);
}
.title_heading {
  max-height: max-content;
  justify-content: center;
}
.mid {
  text-align: end;
}

@media screen and (max-width: 430px) and (max-height:900px) {
.main{
  display: block;

}
.left{
  margin: 0%;
  margin-top: 7vh;
}
.nav{
  width: 100vw;
  height: 7vh;
  z-index: 3;
}
.banner_head{
  display: none;
}
}
